.work-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
  }
  
  nav ul {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  nav li {
    margin-right: 20px;
  }
  
  nav a {
    color: #000;
    text-decoration: none;
  }
  
  nav a:hover {
    text-decoration: underline;
  }
  
  .work-content {
    max-width: 800px;
    margin-top: 40px;
  }
  
  .work-content h1 {
    font-size: 2.5rem;
    margin-bottom: 20px;
  }
  
  .work-content p {
    font-size: 1.25rem;
    line-height: 1.5;
    margin-bottom: 20px;
  }
  
  .work-content ul {
    margin-bottom: 40px;
  }
  
  .work-content ul li {
    font-size: 1.25rem;
    line-height: 1.5;
  }
  