.contact-container {
    background-color: #f5f5f5;
    color: #333;
    height: 100vh;
  }
  
  nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    background-color: white;
  }
  
  nav ul {
    display: flex;
    list-style: none;
  }
  
  nav ul li {
    margin-right: 1rem;
  }
  
  nav a {
    color: #333;
    text-decoration: none;
  }
  
  .contact-content {
    max-width: 600px;
    margin: 0 auto;
    padding: 2rem;
    text-align: center;
  }
  
  .contact-content h1 {
    font-size: 3rem;
    margin-bottom: 2rem;
  }
  
  .contact-content p {
    font-size: 1.5rem;
    margin-bottom: 1rem;
  }
  
  .contact-content h2 {
    font-size: 2rem;
    margin-bottom: 1rem;
  }
  
  .social-media-links {
    display: flex;
    justify-content: center;
    margin-top: 2rem;
  }
  
  .social-media-links li {
    margin-right: 1rem;
  }
  
  .social-media-links a {
    color: #333;
    text-decoration: none;
    font-size: 1.5rem;
  }
  